import { IQuestionPick, IDataStore} from '../App/App';
export default class QuestionPick implements IQuestionPick{
    public constructor() { }

    answerCount(arr: Array<string> ):number {
        /*var answersObjectString = dataStore.lookup(word);
        var answersObject = JSON.parse ( answersObjectString );
        var answersArray = answersObject.userAnswer;*/
        var answersArray = arr;

        var trueCount = 0;
        var falseCount = 0;

        for( var i = 0; i < answersArray.length; i++ )
        {
            if( answersArray[i] == "doğru" )
                trueCount++;
            if( answersArray[i] == "yanlış" )
            //else
                falseCount++;
        }
        //document.write( "Doğru sayısı: " + trueCount + " , " + "Yanlış sayısı: " + falseCount );
        return falseCount;
    }

    falseCounter(dataStore: IDataStore):Array<number> {

        var falseArray: number[] = [];
        for ( var i = 1; i < dataStore.getSize(); i++ ) {
            
            var answersString = dataStore.lookupInt(i);
            var answersObjectString = dataStore.lookup(answersString);
            var answersObject = JSON.parse( answersObjectString );
            var answersArray = answersObject.userAnswer;
    
            var falseCount = this.answerCount(answersArray);
            falseArray.push(falseCount);

        }
        //document.write( falseArray.toString() + "//" );
        falseArray = falseArray.sort();
        return falseArray;
    }

    sortByWeight(dataStore: IDataStore):Array<any> {
        var weightArray: any[] = [];
        for ( var i = 1; i < dataStore.getSize(); i++ ) {
            
            var answersString = dataStore.lookupInt(i);
            var answersObjectString = dataStore.lookup(answersString);
            var answersObject = JSON.parse( answersObjectString );
            weightArray.push(answersObject);

        }
        weightArray = weightArray.sort((a, b) => a.weight.localeCompare(b.name));
        document.write(weightArray.toString());
        return weightArray;
        
    }

    pickQuestion(){
        
    }

}   

